<template>
  <div class="cart">
    <div class="nodata" v-if="!data.cartProductVoList.length">购物车为空</div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        data: {}
      }
    },
    mounted () {
      this.$serve.getCart()
        .then(res => {
          console.log(res)
          if (res.data.status === 0) {
            this.data = res.data.data
          }
        })
    }
  }
</script>



