import axios from 'axios'
import qs from 'qs'

axios.defaults.baseURL = 'http://www.dengty.top:8080/'
// axios.defaults.baseURL = 'http://www.whatoeat.cn/mall/'
// axios.defaults.baseURL = 'http://www.dengty.top:8080'
// axios.defaults.baseURL = 'http://47.94.156.30:8080/mall/'
// axios.defaults.headers.common['Authorization'] = 'AUTH_TOKEN'
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'
axios.defaults.withCredentials = true // 需要此条才能携带cookie

const serve = {
  // 用户模块
  login: function (username, password) {
    return new Promise((resolve, reject) => {
      axios.post('/user/login.do', qs.stringify({
        username,
        password
      }))
        .then(res => {
          resolve(res)
        })
    })
  },
  reg: function (username, password) {
    return new Promise((resolve, reject) => {
      axios.post('/user/register.do', qs.stringify({
        username,
        password
      }))
        .then(res => {
          resolve(res)
        })
    })
  },
  // 购物车
  getCart: function () {
    return new Promise((resolve, reject) => {
      axios.get('/cart/list.do')
        .then(res => {
          resolve(res)
        })
    })
  },
  addToCart: function (id, count) {
    return new Promise((resolve, reject) => {
      axios.post('/cart/add.do?productId=' + id + '&count=' + count)
        .then(res => {
          resolve(res)
        })
    })
  },
  // 商品模块
  getProduct: function (keyword) {
    return new Promise((resolve, reject) => {
      axios.get('/product/list.do?keyword=' + keyword)
        .then(res => {
          resolve(res)
        })
    })
  },
  getDetail: function (id) {
    return new Promise((resolve, reject) => {
      axios.get('/product/detail.do?productId=' + id)
        .then(res => {
          resolve(res)
        })
    })
  }
}

export default serve



