<template>
  <div class="login" v-if="showReg">
    <div class="title">注册</div>
    <div class="close" @click="close">&times;</div>
    <input type="text"v-model="user" name="user" placeholder="请输入用户名">
    <input v-model="pwd" type="password" name="password" placeholder="请输入密码">

    <button type="button" @click="login">注册</button>
  </div>
</template>
<script type="text/javascript">
  export default {
    data () {
      return {
        user: '',
        pwd: ''
      }
    },
    methods: {
      login: function () {
        // 注册
        // alert(this.user, this.pwd)
        this.$serve.reg(this.user, this.pwd)
          .then(res => {
            console.log(res)
          })
      },
      close: function () {
        this.$emit('close')
      }
    },
    props: [
      'showReg'
    ]
  }
</script>
<style type="text/css" scoped>
  .login{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 300px;
    height: 200px;
    position: fixed;
    z-index: 3;
    top:50%;
    left: 50%;
    margin-left: -150px;
    margin-top: -100px;
    /*background: rgba(250,110,80,.2);*/
    border: 1px solid #fc7050;
    background-color: #fff;
  }
  .login .title{
    color: #fc7050;
    font-weight: bold;
    font-size: 1.2rem;
  }
  .login input{
    height: 30px;
    line-height: 30px;
    border: 1px solid #eee;
    box-shadow: none;
    outline: none;
    padding: 0 10px;
  }
  .login button{
    border: none;
    padding: 10px;
    background-color: #fc7050;
    color: #fff;
    outline: none;
  }
  .login .close{
    color: #000;
    position: absolute;
    right: 10px;
    top: 10px;
    font-size: 1.5rem;
  }
  .login .close:hover{
    color: #fc7050;
  }
</style>



