<template>
  <div id="app">
    <div class="sticky">
      <!-- <div class="header"></div> -->
      <header-com></header-com>
      <router-view></router-view>
    </div>
    <div class="footer">
    <p>因时间所限，前端开发仍在跟进</p>
    <p>
      CopyRight By <a>TYT</a> & <a>namelessman</a>
    </p>
    </div>
  </div>
</template>

<script>
import HeaderCom from '@/components/header'
export default {
  name: 'app',
  components: {
    HeaderCom
  }
}
</script>

<style>
*{
  margin: 0;
  padding: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
html,body,#app{
  height: 100%;
}
.sticky{
  min-height:100%;
  margin-bottom: -50px;
}
.footer{
  height: -50px;
}
.footer p:first-child{
  font-size: 12px;
  line-height: 1.5rem;
}
</style>



