const store = {
  get: function (key) {
    return sessionStorage[key]
  },
  set: function (key, value) {
    sessionStorage.setItem(key, value)
  },
  del: function (key) {
    sessionStorage.removeItem(key)
  },
  clear: function () {
    sessionStorage.clear()
  }
}

export default store



