<template>
  <div class="list">
    <div class="tip" v-if="data.list && !data.list[0]">店里还没有您想要的商品，换个关键词试试？</div>
    <div v-for="item in data.list">
<router-link :to="'/detail/' + item.id">
  <div class="item">
    <img :src="'http://img.happymmall.com/' + item.mainImage" alt="">
    <div class="name">{{item.name}}</div>
    <div class="price">￥{{item.price}}</div>
    <div class="subtitle">{{item.subtitle}}</div>
  </div>
</router-link>
    </div>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        data: {}
      }
    },
    mounted: function () {
      console.log(this.$route.query)
      if (this.$route.query && this.$route.query && this.$route.query.keyword) {
        this.keyword = this.$route.query.keyword
      }
      this.$serve.getProduct(this.keyword)
        .then(res => {
          console.log(res.data.data)
          this.data = res.data.data
        })
    }
  }
</script>
<style scoped>
  .list{
    display: flex;
  }
  .item{
    width: 200px;
    height: 300px;
    border: 2px solid #eee;
    margin: 10px;
    cursor: pointer;
  }
  .item:hover {
    border: 2px solid #fc7050;
  }
  .item img{
    width: 100%;
    height: 200px;
  }
  .name {
    font-weight: bold;
    font-size: 14px;
    overflow : hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }
  .subtitle{
    font-size: 12px;
    color: #888;
  }
  .price{
    color: #fc7050;
    font-weight: bold;
  }
  .tip{
    text-align: center;
    width: 100%;
    position: absolute;
    top: 50%;
  }
</style>



