<template>
  <div class="detail">
    <!-- 这里是详情页 -->
    <div class="top">
      <div class="swiper-c">
        <swiper>
          <swiper-slide v-for="subImage in data.subImages">
            <img :src="'http://img.happymmall.com/'+subImage" alt="">
          </swiper-slide>
        </swiper>
      </div>
      <div class="info">
        <div class="name">{{data.name}}</div>
        <div class="description">{{data.subtitle}}</div>
        <div class="price">价格：<span>￥{{data.price}}</span></div>
        <div class="stock">库存：{{data.stock}}</div>
      
        <!-- 数量和添加 -->
        <div class="num">购买数量：
          <button @click='num>1?num--:""'>-</button>
          <input v-model.number="num" type="text">
          <button @click="num<data.stock?num++:''">+</button>
        </div>
        <button type="button" class="add" @click="addToCart">添加到购物车</button>
      </div>
    </div>
    <!-- 详情 -->
    <div class="detail-title">商品详情</div>
    <div class="item" v-html="data.detail">
      
    </div>
  </div>
</template>
<script>
  import eventbus from '@/assets/js/eventbus'
  import store from '@/assets/js/store'
  export default {
    data () {
      return {
        data: {},
        num: 0
      }
    },
    mounted: function () {
      this.$serve.getDetail(this.$route.params.id)
        .then(res => {
          if (res.data.status === 0) {
            console.log(res.data.data)
            let arr = res.data.data.subImages.split(',')
            // console.log(arr)
            this.data = res.data.data
            this.data.subImages = arr
          }
        })
    },
    methods: {
      addToCart: function () {
        if (store.get('username')) {
          console.log(this.data)
          this.$serve.addToCart(this.data.id, this.num)
            .then(res => {
              console.log('data', res)
              if (res.data.status === 0) {
                eventbus.$emit('addToCart', {id: this.data.id, count: this.num})
              }
            })
        }
      }
    }
  }
</script>
<style scoped>
  .detail{
    max-width: 1000px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    padding-top: 20px;
    text-align: left;
  }
/*  .detail>div{
    width: 100%;
  }*/
  .top{
    display: flex;
    flex-direction: row;
    height: 400px;
    overflow: hidden;
    margin-bottom: 50px;
  }
  .swiper-c{
    width: 400px;
    height: 400px;
    margin-right: 20px;
  }
  .swiper-c img{
    width: 100%;
  }
  /*info*/
  .info{
    display: flex;
    flex-direction: column;
    line-height: 2rem;
  }
  .name{
    font-weight: bold;
    /*text-align: left;*/
  }
  .description{
    font-size: 14px;
    color: #fc7050;
    /*text-align: left;*/
  }
  .price span{
    color: #fc7050;
    font-size: 1.2rem;
  }
  .stock{
    font-size: 14px;
  }
  .num{
    display: flex;
    /*justify-content: center;*/
    margin-bottom: 20px;
  }
  .num button{
    width: 30px;
    border: none;
    -webkit-box-shadow: none;
    box-shadow: none;
    outline: none;
    cursor: pointer;
    background-color: #fc7050;
    color: #fff;
  }
  .num input{
    width: 50px;
    text-align: center;
    border: 1px solid #fc7050;
    border-left: none;
    border-right: none;
    outline: none;
    -webkit-box-shadow: none;
    box-shadow: none;
  }
  .add {
    width: 100px;
    height: 40px;
    background-color: #fc7050;
    color: #fff;
    cursor: pointer;
  }
  .detail-title{
    height: 60px;
    background-color: #fc7050;
    color: #fff;
    font-weight: bold;
    line-height: 60px;
    padding-left: 20px;
    margin-bottom: 10px;
  }
</style>



