<template>
  <div class="header">
      <div class="r">
        <div class="name" v-if="showN">
          <div class="cart"><router-link to="/cart">购物车<span v-if="productionCount">{{productionCount}}</span></router-link></div>
          <div>欢迎您，{{username}}</div>
        </div>
      </div>
    <div class="r" v-if="!showN">
      <div class="reg" @click="reg">注册</div>
      <div class="login" @click="showLogin">登录</div>
      <login :showLogin="showL" v-on:close="showL=false"></login>
      <reg :showReg="showR" v-on:close="showR=false"></reg>
    </div>
  </div>
</template>
<script type="text/javascript">
  import login from '@/components/login'
  import reg from '@/components/reg'
  import EventBus from '@/assets/js/eventbus'
  import store from '@/assets/js/store'
  export default {
    data () {
      return {
        showL: false,
        showR: false,
        // showN: false,
        showN: store.get('username'),
        username: store.get('username'),
        productionCount: 0
      }
    },
    mounted: function () {
      // 监控购物车
      EventBus.$on('addToCart', (params) => {
        console.log(params)
        this.$serve.getCart()
          .then(res => {
            console.log('cart', res.data)
            this.productionCount = 0
            for (let i = 0; i < res.data.data.cartProductVoList.length; i++) {
              this.productionCount += res.data.data.cartProductVoList[i].quantity
            }
          })
      })
    },
    methods: {
      reg: function () {
        this.showL = false
        this.showR = true
      },
      showLogin: function () {
        // alert('login')
        this.showR = false
        this.showL = true
        EventBus.$on('login', (params) => {
          this.showN = true
          this.username = params
          store.set('username', params)
        })
      }
    },
    components: {
      login,
      reg
    }
  }
</script>
<style type="text/css" scoped>
.header{
  display: flex;
  padding: 10px;
  justify-content: space-between;
  background-color: #fc7050;
  color: #fff;
}
.r{
  width: 100%;
  display: flex;
  justify-content: flex-end;
}
.r>div {
  padding: 0 20px;
  cursor: pointer;
}
.name{
  display: flex;
}
.name .cart{
  margin-right: 20px;
}
a{
  color: inherit;
  text-decoration: none;
}
</style>



