<template>
  <div class="search">
    <input @keydown.enter="search" v-model="keyword" type="text" placeholder="请输入商品名">
    <button type="button" @click="search">搜索</button>
  </div>
</template>
<script>
  import router from '@/router/'
  export default {
    data () {
      return {
        keyword: ''
      }
    },
    methods: {
      search: function () {
        router.push({path: '/list?keyword=' + this.keyword})
      }
    }
  }
</script>
<style scoped>
  .search{
    display: flex;
    justify-content: center;
    height: 40px;
  }
  .search input{
    width: calc(100% - 60px);
    outline: none;
    box-shadow: none;
    border: 1px solid #fc7050;
    padding: 0 10px;
  }
  .search button {
    width: 60px;
    border: none;
    outline: none;
    background-color: #fc7050;
    color: #fff;
    cursor: pointer;
  }
</style>



