<template>
  <div class="menu">
    <ol>
      <li v-for="m in menu">
        <span v-for="n in m">
          <router-link :to='"/list?keyword="+n'>{{n}}</router-link>
        </span>
      </li>
    </ol>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        menu: [
          ['手机', '数码'],
          ['手机', '数码'],
          ['手机', '数码'],
          ['手机', '数码'],
          ['手机', '数码'],
          ['手机', '数码']
        ]
      }
    }
  }
</script>

<style scoped>
  .menu{
    background-color: #f3f3f3;
  }
  ol{
    list-style: none;
  }
  li{
    line-height: 2rem;
  }
  li span{
    margin-right: 10px;
  }
  li span:hover{
    color: #fc7050;
    cursor: pointer;
  }
  a{
    color: inherit;
    text-decoration: none;
  }
</style>



