              }
            ]
          }
        ]
      }
    },
    mounted: function () {
      // this.$serve.getProduct()
      //   .then(res => {
      //     console.log(res)
      //   })
    },
    methods: {
      goDetail: function () {

      }
    },
    components: {
      menucom,
      search
    }
  }
</script>
<style scoped>
  .wrapper{
    max-width: 1000px;
    margin: 0 auto;
    /*text-align: center;*/
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .top{
    display: flex;
    justify-content: center;
  }
  .menu {
    width:200px;
    position: relative;
    display: inline-block;
  }
  .swiper-parent{
    max-width: 790px;
  }
  .swiper-parent img{
    width: 100%;
  }
  .search {
    width: 60%;
    margin: 20px 0;
  }
</style>



